export default {
  name: 'PaymentModal',

  props: {
    visible: {
      type: Boolean,
      required: true,
    },
  },

  data() {
    return {
      loading: false,
    }
  },

  methods: {
    showModal() {
      this.visible = true
    },

    handleOk(e) {
      this.$emit('verify')
    },

    handleCancel(e) {
      this.$emit('closeModal')
    },

    closeModal() {
      this.$emit('closeModal')
    },
  },
}
